  background-repeat: no-repeat;
}

.logo {
  font-family: 'Rubik Dirt', cursive;
  text-align: center;
  background: rgb(8, 143, 143);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

img {
  text-align: center;
  /* flex-shrink: 0; */

  height: 30px;
}

h1 {
  margin-right: 150px;
  /* margin-left: 25px; */
  /* width: 100px; */
  /* flex-shrink: 0; */
}

h2 {
  text-align: center;
  font-size: xx-large;
  font-family: 'Fasthand', cursive;
  color: whitesmoke;
}

.details {
  background-color: rgba(230, 227, 232, 0.25);
  padding: 20px 10px;
  margin: 4px;
  border-radius: 5%;
  text-align: left;
  padding-left: 80px;
  color: antiquewhite;
}

.btn {

  /* background-color: #ae30e4; */
  background-color: rgb(8, 143, 143);

  border-color: rgb(8, 143, 143);
  color: whitesmoke;
  margin-top: 30px;
  margin-bottom: 50px;
  border-radius: 5%;
  font-size: x-large;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 7px;
  padding-right: 7px;


}

.order {
  background-color: rgb(8, 143, 143);

  border-color: rgb(8, 143, 143);
  color: whitesmoke;
  margin-top: 30px;
  margin-bottom: 50px;
  border-radius: 5%;
  font-size: x-large;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 7px;
  padding-right: 7px;
}

.orderlist {
  background-color: rgba(230, 227, 232, 0.25);
  padding: 20px 10px;
  margin: 4px;
  border-radius: 5%;
  color: antiquewhite;
  text-align: left;
  margin-top: 30px;
}

.ordernumber {
  font-size: larger;
  font-family: 'Times New Roman', Times, serif;
  text-align: center;
}